import { Location } from '@angular/common';
import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { MatListModule } from '@angular/material/list';
import { APP_ROUTE } from '@core/constants';
import { SubscriptionStatus, SubscriptionTier, SubscriptionType } from '@core/enums/profile.enum';
import { SnackService } from '@core/services/snack.service';
import { SubscriptionOffersComponent } from '@shared/components/subscription-offers/subscription-offers.component';
import { MyProfileFacade } from '@feature/my-profile/my-profile.facade';
import { CancelSubscriptionDialogComponent } from '@feature/my-profile/v2/components/cancel-subscription-dialog/cancel-subscription-dialog.component';
import { ChangeCardDilaogComponent } from '@feature/my-profile/v2/components/subscription/change-card-dilaog/change-card-dilaog.component';
import { ButtonType } from '@shared/components/button/button.component';
import { ButtonModule } from '@shared/components/button/button.module';
import { SharedModule } from '@shared/shared.module';
import { NgxSkeletonLoaderModule } from 'ngx-skeleton-loader';

@Component({
    selector: 'findqo-subscription-plan',
    templateUrl: './subscription-plan.component.html',
    styleUrl: './subscription-plan.component.scss',
    imports: [SharedModule, MatListModule, ButtonModule, SubscriptionOffersComponent, NgxSkeletonLoaderModule]
})
export class SubscriptionPlanComponent implements OnInit {
	constructor(
		public snackBarService: SnackService,
		public dialog: MatDialog,
		protected profileFacade: MyProfileFacade,
		protected readonly location: Location
	) {}

	//TODO: API not implemented for this, updated when API will be ready
	canChangeCard = false;
	readonly subscriptionTier = SubscriptionTier;
	readonly appRoute = APP_ROUTE;
	readonly buttonType = ButtonType;
	readonly subscriptionStatus = SubscriptionStatus;
	readonly subscriptionType = SubscriptionType;

	readonly benefitsList = ['n3886', 'n3891', 'n3892'];

	readonly themeList = [
		{ border: '4', width: '100%', margin: '0', height: '40px' },
		{ border: '4', width: '100%', margin: '0', height: '20px' },
		{ border: '4', width: '100%', margin: '0', height: '20px' },
		{ border: '4', width: '100%', margin: '0', height: '20px' },
		{ border: '4', width: '100%', margin: '0', height: '20px' },

		{ border: '4', width: '100%', margin: '0', height: '40px' },
		{ border: '4', width: '100%', margin: '0', height: '20px' },
		{ border: '4', width: '100%', margin: '0', height: '20px' },
		{ border: '4', width: '100%', margin: '0', height: '20px' },
		{ border: '4', width: '100%', margin: '0', height: '20px' },

		{ border: '4', width: '100%', margin: '0', height: '40px' },
		{ border: '4', width: '100%', margin: '0', height: '20px' },
		{ border: '4', width: '100%', margin: '0', height: '20px' },
		{ border: '4', width: '100%', margin: '0', height: '20px' },
		{ border: '4', width: '100%', margin: '0', height: '20px' },

		{ border: '4', width: '100%', margin: '0', height: '40px' },
		{ border: '4', width: '100%', margin: '0', height: '20px' },
		{ border: '4', width: '100%', margin: '0', height: '20px' },
		{ border: '4', width: '100%', margin: '0', height: '20px' },
		{ border: '4', width: '100%', margin: '0', height: '20px' },
	];

	cancleSubscription(): void {
		this.dialog
			.open(CancelSubscriptionDialogComponent, {
				disableClose: true,
				maxWidth: '500px',
				width: '95%',
				data: this.profileFacade.getSubscriptionData(),
			})
			.afterClosed()
			.subscribe((res) => {
				if (res.value) {
					this.profileFacade.cancelSubscription(res.value).subscribe(() => {
						this.snackBarService.open({
							data: { message: 'Subscription cancelled', type: 'success' },
							panelClass: 'snackbar-success',
						});
					});
				}
			});
	}

	changeCard(): void {
		this.dialog.open(ChangeCardDilaogComponent, {
			disableClose: true,
			maxWidth: '500px',
			backdropClass: 'inquiry-backdrop',
		});
	}

	ngOnInit(): void {
		if (!this.profileFacade.getSubscriptionData()) {
			this.profileFacade.getSubscriptionInfo().subscribe();
		}
	}
}
